<template>
  <div class="body-page" :style="{'background-image': `url('${publicPath}image/loginBG.png')`}">
    <div class="content-box">
      <h1>User</h1>
      <div class="select-box">
        <div :class="{'select-btn': true, active: active === 1 }" @click="active=1">Login</div>
        <div :class="{'select-btn': true, active: active === 2 }" @click="active=2">Register</div>
      </div>
      <template v-if="active === 1">
        <form>
          <label>Email:</label>
          <input v-model="loginData.email" type="text">
          <div class="tip">Please include “@life.hkbu.edu.hk” or “@hkbu.edu.hk”.</div>
          <label>Password:</label>
          <input v-model="loginData.password" type="password">
          <div class="tip">The password is not necessary the same as that for SSOid.</div>
        </form>
        <div class="btn-box">
          <button @click="loginStart">Login</button>
          <div class="text-info">Forget  Password</div>
          <button @click="goLink('/')">Login As Guest </button>
        </div>
      </template>
      <template v-else>
        <form>
          <label>Email<span>*</span>:</label>
          <input v-model="registerData.email" type="text">
          <label>Password<span>*</span>:</label>
          <input v-model="registerData.password" type="password">
          <label>Confirm Password<span>*</span>:</label>
          <input v-model="rePassword" type="password">
          <label>First Name<span>*</span>:</label>
          <input v-model="registerData.first_name" type="text">
          <label>Last Name<span>*</span>:</label>
          <input v-model="registerData.last_name" type="text">
          <label>Concentration Studies<span>*</span>:</label>
          <select v-model="registerData.concentration_study_id">
            <option v-for="item in concentrationOption" :key="'concentrationOption' + item.id" :value="item.id">
              {{ item.content }}
            </option>
          </select>
          <label>Year of Entry<span>*</span>:</label>
          <select v-model="registerData.entry_year">
            <option v-for="item in 40" :key="item" :value="1979 + item">
              {{ 1979 + item }}
            </option>
          </select>
        </form>
        <div class="btn-box">
          <button @click="registerStart">Next</button>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
import { userLogin, concentrationList, createUser } from '@/api'
import { setToken } from '@/cookie/token'
import { Toast } from 'vant'
export default {
  data() {
    return {
      publicPath: process.env.BASE_URL,
      active:1,
      concentrationOption: [],
      loginData: {
        email: null,
        password: null
      },
      registerData: {
        email: null,
        first_name: null,
        last_name: null,
        role: 3,
        password: null,
        concentration_study_id: null,
        entry_year: null
      },
      rePassword: null
    }
  },
  created() {
    concentrationList().then(res => {
      this.concentrationOption = res.data
    }).catch(() => {})
  },
  methods: {
    goLink(path) {
      this.$router.push({ path })
    },
    loginStart() {
      if (!this.loginData.email) {
        Toast('email is required')
        return
      }
      if (!this.loginData.password) {
        Toast('password is required')
        return
      }
      userLogin(this.loginData).then(res => {
        setToken(res.api_token)
        Toast({
          type: 'success',
          message: 'login is successed'
        })
        this.goLink('/')
        setTimeout(() => {
          window.location.reload()
        }, 1000)
      }).catch(() => {})
    },
    registerStart() {
      if (!this.registerData.email) {
        Toast('email is required')
        return
      }
      if (!this.registerData.first_name) {
        Toast('first_name is required')
        return
      }
      if (!this.registerData.last_name) {
        Toast('last_name is required')
        return
      }
      if (!this.registerData.password) {
        Toast('password is required')
        return
      }
      if (!this.registerData.concentration_study_id) {
        Toast('concentration_study is required')
        return
      }
      if (!this.registerData.entry_year) {
        Toast('entry_year is required')
        return
      }
      if (this.rePassword !== this.registerData.password) {
        Toast('The two passwords are inconsistent')
        return
      }
      createUser(this.registerData).then(res => {
        setToken(res.api_token)
        Toast({
          type: 'success',
          message: 'register is successed'
        })
        this.goLink('/')
        setTimeout(() => {
          window.location.reload()
        }, 1000)
      }).catch(() => {})
    }
  }
}
</script>
